<template>
	<div class="saleedi-bindEnt">
		<div class="bindEnt-form">
			<van-cell-group class="bindEnt-cell-group" :border="false">
				<van-field
					v-model="entName"
					ref="entName"
					label="订货企业"
					class="bindEnt-cell"
					placeholder="请输入订货企业名称"
					:error-message="errorMessage.entName"
					@keyup.enter.native="onKeyupInput('entName')">
				</van-field>
				<van-field
					v-model="customerInfo"
					ref="customerInfo"
					label="客户信息"
					class="bindEnt-cell"
					placeholder="请输入您在订货企业中的轻订货注册码"
					:error-message="errorMessage.customerInfo"
					@keyup.enter.native="onKeyupInput('customerInfo')">
				</van-field>
			</van-cell-group>
		</div>
		<div v-show="!$route.query.notFirst">
			<van-button class="bind-btn" type="primary" round @click="firstBindEntHandle">绑定订货企业</van-button>
			<van-button class="bind-btn" type="warning" round @click="skipHandle">跳过</van-button>
		</div>
		<div v-show="$route.query.notFirst">
			<van-button class="bind-btn" type="primary" round @click="bindEntHandle">绑定订货企业</van-button>
		</div>
	</div>
</template>

<script>
import { inputAutoFocusMoblie } from '@/js/lib/form'
export default {
	name: 'bindEnt',
	data () {
		return {
			formFieldRef: ['entName', 'customerInfo'],
			entName: '',
			customerInfo: '',
			errorMessage: {
				entName: '',
				customerInfo: ''
			}
		}
	},
	methods: {
		onKeyupInput (refName) {
			inputAutoFocusMoblie.apply(this, [refName, this.formFieldRef]).then(() => {
				this.bindEntHandle()
			})
		},
		firstBindEntHandle () {
			const params = {
				companyName: this.entName,
				code: this.customerInfo
			}
			this.$fxHttp.requestPost({
				url: '/bingCompany',
				data: params
			}).then(res => {
				this.$router.push('/home/find')
			})
		},
		skipHandle () {
			this.$router.push('/home/find')
		},
		bindEntHandle () {
			console.log('绑定')
		}
	}
}
</script>
<style lang="stylus" scoped>
.saleedi-bindEnt {
	height:100%
	background:#fff
	.bindEnt-form {
		width:350px
		margin: 0 auto
		.bindEnt-cell-group {
			padding-top: 30px
			padding-bottom: 10px
		}
	}
	.bind-btn {
		margin-top:20px
		width:320px
	}
}
</style>
